<template>
	<div class="book-deta">
		<div class="login-header">
			<div @click="routerGo" class="back">
				<i></i>
				<span>返回</span>
			</div>
			
			<h1>书籍详情</h1>
			
			<router-link to="/">
				<span class="login-home">
					<i></i>
				</span>
			</router-link>
		</div>
		
		<div class="book-clearfix" >
			<img :src="[link + bookId.cover]" >
			<div class="clearfix-right">
				<h1>{{bookId.title}}</h1>
				<p>
					<span>{{bookId.author}}</span><i>|</i>
					<span>{{bookId.cat}}</span><i>|</i>
					<span>{{bookId.wordCount}}万字</span>
				</p>
				<p>3天前更新</p>
			</div>
		</div>
		
		<div class="read-link">
			<span v-if="koko" class="join" @click="addButton()">加入书架</span>
			<span v-else class="join-false" @click="addCancel()" >撤出书架</span>
			<span class="button">
				<router-link :to="/reading/ + bookId.title + '/' + bookId.lastChapter + '/' +  ' '">
					开始阅读
				</router-link>
			</span>
		</div>
		
		<div class="read-data"> 
			<p>
				<span>追人气</span><br/>
				<i>{{bookId.latelyFollower}}万</i>
			</p>
			<p>
				<span>读者留存率</span><br/>
				<i>{{bookId.retentionRatio}}%</i>
			</p>
			<p>
				<span>日更字数/天</span><br/>
				<i>{{bookId.serializeWordCount}}</i>
			</p>
		</div>
		
		<div class="introduction">
			<p>{{bookId.longIntro}}</p>
		</div>
		
		<router-link :to="/bookchapter/ + bookId._id + '/' + bookId.title">
			<div class="inner-clearfix">
				目录
				<i></i>
				<span>[3天前更新] {{bookId.lastChapter}}</span>
			</div>
		</router-link>
		
		
		<div class="hot-comment">
			<div class="hot-comment-top">
				<h4>热门书评</h4>
				<span>更多评论</span>
			</div>
			<div class="hot-comment-inner" v-for="(c,com) in commentId" :key="com">
				<img :src="[link + c.author.avatar]" class="auto-img">
				<div class="hot-comment-inner-right">
					<p class="name">{{c.author.nickname}}</p>
					<p class="title">{{c.title}}</p>
					<p>*****</p>
					<p class="content">{{c.content}}</p>
					<p>
						<span>21分钟前</span>
						<i>{{c.likeCount}}人觉得有用</i>
					</p>
				</div>
			</div>
		</div>
		<ElmTail></ElmTail>
	</div>
</template>

<script>
	
	import ElmTail from '../components/ElmTail'
	export default {
		name:"BookDeta",
		data(){
			return{
				bookId:[],
				commentId:[],
				addbutton:true,
				link:'http://statics.zhuishushenqi.com',
			}
		},
		
 		created(){
 		},
		beforeRouteEnter(to,from,next){
			
			next(vm => {
				vm.axios.get(vm.CommentId).then(c => {
					vm.commentId = c.data.reviews.slice(0,2)
				})
			})
			next(vm => {
				vm.axios.get(vm.BookId).then(b => {
					vm.bookId = b.data
					console.log(vm.bookId);
				})
			})
		},
		
		
		methods:{
			routerGo(){
				this.$router.go(-1);
			},
			addButton(){
				this.addbutton = true
				let index = this.$store.state.addBook.indexOf(this.bookId)
				if(index == -1){
					this.$store.commit('addBook', this.bookId)
				}
			},
			addCancel(){
				this.addbutton = false
				let index = this.$store.state.addBook.indexOf(this.bookId)
				if(index != -1){
					this.$store.commit('addCancel', index)
				}
			},
// 			reload () {
// 				this.addbutton = true
// 				this.$nextTick(() => (this.addbutton = false))
// 			}   
// 
		},
		computed:{
			CommentId(){
				let urls = `no/book/reviews?book=${this.$route.params.name}`
				return urls
			},
			BookId(){
				let urlId = `api/book/${this.$route.params.name}`
				return urlId
			},
			koko(){
				if(!this.$store.state.addBook.length){
					return true
				}
				for(let i = 0; i < this.$store.state.addBook.length; i++){
					
					if(this.bookId == this.$store.state.addBook[i]){
						return false
					}
				}
				return true
			}
			
		},
		components:{
			ElmTail
		}
	}
	
</script>

<style lang="scss" rel="stylesheet/scss" scoped>
	@import "../../public/sass/mixin";
	a{
		color: #fff;
	}
	.book-deta{
		background-color: #f4f4f4;
		position: absolute;
		left: 0;
		top: 0;
		bottom: 0;
		right: 0;
	}
	
	.login-header{
		width: 100%;
		height: 2.75rem;
		background-color: #b93321;
		color: #fff;
		display: block;
	
		.back{
			display: inline-block;
			text-align: center;
			height: 100%;
			width: 60px;
			float: left;
			
			i{
				display: inline-block;
				background: url(../../public/img/arror-back.png) no-repeat center;
				background-size: 125%;
				height: 100%;
				width: 0.9375rem;
				vertical-align: middle;
			}
			span{
				vertical-align: middle;
				font-size: 12px;
				margin-left: 2px;
				color:#fff;
			}
		}
		
		h1{
			width: 70%;
			display: inline-block;
			text-align: center;
			line-height: 2.75rem;
		}
		
		.login-home{
			background: url(../../public/img/home.png) no-repeat center;
			background-size: 35%;
			height: 100%;
			width: 14%;
			float: right;
			display: inline-block;
		}
		
	}
	
	.book-clearfix{
		width: 100%;
		height: 8rem;
		padding: 1.25rem;
		background-color: #fff;

		img{
			float: left;
			width: 4.0625rem;
			height: 100%;
			margin-right: 0.625rem;
		}
	}
	
	.clearfix-right{
		float: right;
		width: 77%;
		
		h1{
			font-weight: 400;
			font-size: 16px;
		}
		p{
			margin-top: 10px;
			font-size: 12px;
			color: #999;
		}
		span:nth-child(1){
			color: #b93321;
		}
		i{
			margin: 0 0.625rem;
		}
	}

	.read-link{
		width: 100%;
		height: 4.0625rem;
		display: flex;
		border-bottom: 1px solid #ebebeb;
		background-color: #fff;
		
		
		span{
			flex:0 0 9.375rem;
			height: 2.75rem;
			margin: 0 auto;
			line-height: 2.75rem;
			text-align: center;
			border-radius: 0.125rem;
		}
		.join{
			background-color: #fff;
			color: #b93321;
			border: 1px solid #b93321;
		}
		.button{
			background-color: #b93321;
		}
		.join-false{
			border: 1px solid #999;
			background: #999;
			color: #fff;
		}
	}

	.read-data{
		height: 5.25rem;
		border-bottom: 1px solid #ebebeb;
		display: flex;
		background-color: #fff;
		
		p{
			flex: 1;
			text-align: center;
			margin: auto 0;
			
			span{
				color: #999;
				font-size: 12px;
			}
			i{
				display: inline-block;
				margin-top: 0.5rem;
				font-size: 16px;
			}
		}
	}

	.introduction{
		padding: 1.25rem;
		border-bottom: 1px solid #ebebeb;
		background-color: #fff;
		
		p{
			font-size: 0.8125rem;
			-webkit-line-clamp: 3;
			-webkit-box-orient: vertical;
			text-overflow: ellipsis;
			overflow: hidden;
			display: -webkit-box; 
			line-height: 1.25rem;
		}
	}
	
	.inner-clearfix{
		padding: 1.25rem;
		border-bottom: 1px solid #ebebeb;
		font-size: 0.875rem;
		color: #333;
		background-color: #fff;
		
		span{
			float: right;
			color: #999;
			font-size: 12px;
			overflow: hidden;
			display: -webkit-box;
			-webkit-line-clamp:1;
			-webkit-box-orient:vertical;
			text-overflow: ellipsis;
			max-width: 60%;
			line-height: 0.875rem;
		}
		i{
			float: right;
			background: url(../../public/img/downArror.png) no-repeat center;
			background-size: 60%;
			transform: rotate(-90deg);
			width: 0.9375rem;
			height: 0.9375rem;
			
		}
	}

	.hot-comment{
		padding: 0.8125rem 1.25rem;
		border-bottom: 1px solid #ebebeb;
		background-color: #fff;
		margin-bottom: 0.625rem;
		
		
		.hot-comment-top{
			font-weight: 400;
			margin-bottom: 0.8125rem;
			
			h4{
				display: inline-block;
				font-size: 0.875rem;
			}
			span{
				float: right;
				font-size: 0.75rem;
				color: #999;
			}
		}
	}
	
	.hot-comment-inner{
		position: relative;
		border-bottom: 1px solid #ebebeb;
		margin-bottom: 0.625rem;
		
		img{
			width: 2.1875rem;
			height: 2.1875rem;
			border-radius: 50%;
			position: absolute;
			left: 0;
			top: 0;
			
		}
	}
	
	.hot-comment-inner-right{
		margin-left: 2.5rem;
		margin-bottom: 0.625rem;
		
		font-size: 0.75rem;
		color: #999;
		
		p{
			text-overflow: ellipsis;
			margin-bottom: 0.5375rem;
		}
		.name{
			color: #a58d5e;
		}
		.title{
			color: #000;
			font-weight: 700;
			line-height: 0.875rem;
			display: -webkit-box;
			-webkit-line-clamp: 1;
			-webkit-box-orient: vertical;
			overflow: hidden;
		}
		
		.content{
			display: -webkit-box;
			-webkit-line-clamp: 2;
			-webkit-box-orient: vertical;
			overflow: hidden;
			line-height: 1rem;
		}
		
		i{
			float: right;
		}
	}
</style>
